.country{border-radius:15px;border:1px solid #e6e6e6;width:100%;margin:1em 0;position:relative;padding:0.7em;display:flex;align-items:center}.country p{width:9em;overflow:hidden}.country .percent{position:relative;width:calc(100% - 20.8em);display:flex;align-items:center;height:30px;border-radius:1em;background:#f3f3f3;overflow:hidden}.country .percent .progress{position:absolute;top:0;left:0;width:70%;height:100%}.country .percent .cant{position:absolute;font-weight:600;right:1em}.country .goToStats{margin:auto;border-radius:15px;border:1px solid #1eb2a6;color:#1eb2a6;text-align:center;padding:0.4em 0.7em;font-weight:600;text-transform:uppercase;width:9em}.country .goToStats:hover{background:#1eb2a6;color:white}#containerOrder{margin-top:1em;display:flex;width:100%;justify-content:space-around}#containerOrder button{width:11em;padding:0.3em 0.6em;font-size:0.7em;text-transform:uppercase;font-weight:600;border-radius:10px;background:white;color:#1eb2a6;border:1px solid #1eb2a6}#containerOrder button:hover{background:#1eb2a6;color:white}
