#CountriesContainer{padding:1em;display:flex;flex-direction:column;justify-content:center;align-items:center}#CountriesContainer h1{text-align:center;text-transform:uppercase}#CountriesContainer input{width:70vw;margin:2em;border-radius:15px}#CountriesContainer #filterContainer{width:80vw}#CountriesContainer .contentWrapper{margin:0.5em;display:flex;width:100%;align-items:center;justify-content:space-evenly}#CountriesContainer .contentWrapper button{display:flex;flex-direction:column;align-items:center;justify-content:center;width:9em;height:9em;border-radius:50%;border:2px solid #1eb2a6;background:transparent}#CountriesContainer .contentWrapper button svg{width:3em;height:3em}#CountriesContainer .contentWrapper button p{text-transform:uppercase;font-weight:600}#CountriesContainer .contentWrapper button:hover{opacity:80%}#CountriesContainer .contentWrapper button:hover p{color:#1eb2a6}#CountriesContainer .aclarationWrapper{display:flex;flex-direction:column;margin:1em}#CountriesContainer .aclarationWrapper .aclarationContainer{display:flex;align-items:center}#CountriesContainer .aclarationWrapper .aclarationContainer .bad{width:1em;height:1em;background:#d63447;border-radius:50%}#CountriesContainer .aclarationWrapper .aclarationContainer .good{width:1em;height:1em;background:#1eb2a6;border-radius:50%}#CountriesContainer .aclarationWrapper .aclarationContainer p{margin-left:0.5em;font-style:italic;color:#5b5656}@media only screen and (min-device-width: 320px) and (max-device-width: 556px){#CountriesContainer .aclarationWrapper{display:none}}
