#LoadingContainer{height:99vh;display:flex;justify-content:center;align-items:center;width:100%}#LoadingContainer div{border:1 solid #f9f9f9;border-top-color:#1C2331;border-top-style:groove;height:100px;width:100px;border-radius:50%;margin:auto;animation:girar 1.5s linear infinite}@keyframes girar{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}
