#navbar{background:#79bac1;position:relative;display:flex;flex-wrap:wrap;align-items:center;justify-content:space-between;box-shadow:0px 0.1em 0.3em rgba(0,0,0,0.7)}#navbar svg{padding:0.5em;width:3em;height:3em}#navbar #serchContainer{display:flex;height:40px;align-items:stretch}#navbar #serchContainer input{margin-right:1em;height:auto;border-radius:0 15px 15px 0}#navbar #serchContainer button{background:#e6e6e6;border:none;height:auto;border-radius:15px 0 0 15px;cursor:pointer}#navbar #serchContainer button svg{height:2em;width:2em}#navbar #serchContainer button:hover{opacity:0.9}
