#StatsContainer{width:100%;min-height:calc(100vh - 55px);display:flex;align-items:center;flex-direction:column}#StatsContainer h1{margin:0.5em;font-size:2.2em;text-transform:uppercase}#StatsContainer .pieWrapper{display:flex;flex-wrap:wrap;align-items:stretch;margin:1em;width:90vw}#StatsContainer .pieWrapper #anyPie{height:24em;width:24em}#StatsContainer .pieWrapper .daysContainer{display:flex;flex-wrap:wrap;justify-content:center;align-items:center;overflow:auto;height:24em;margin-bottom:1em;background:#f9f9f9;flex:1}#StatsContainer .pieWrapper .daysContainer h1{width:100%;padding-left:0.5em}#StatsContainer .pieWrapper .daysContainer .day{margin:1em;height:10em;width:10em;border:2px solid #00A8CC;background:white;border-radius:1em;display:flex;flex-direction:column;justify-content:center;align-items:center;cursor:pointer}#StatsContainer .pieWrapper .daysContainer .day span{font-size:1.6em;font-weight:600}#StatsContainer .pieWrapper .daysContainer .day:hover{background:#00A8CC}#StatsContainer .pieWrapper .daysContainer .day:hover span{color:white}#StatsContainer #anyArea{width:90vw;height:20em;margin:1em}#StatsContainer #anyArea .action-voronoi{stroke-width:6}
